import React from "react"
import logo from "../imgs/logo.jpg"

class Nav extends React.Component{

    render(){
        return(
            <nav id="navbar" className="col-12 navbar navbar-expand-md navbar-dark fixed-top animated bounceIn">
                <a href="#">
                   <img className="nav-brand" height="50px" src={logo} />
                </a>
                <a href="#" target="_blank">
                    <i className="fab fa-soundcloud fa-lg icon"></i>
                </a>
                <a href="#" target="_blank">
                    <i className="fab fa-youtube fa-lg icon"></i>
                </a>
                <a href="#" target="_blank">
                    <i className="fab fa-twitter fa-lg icon"></i>
                </a>
                <a href="#" target="_blank">
                    <i className="fab fa-facebook fa-lg icon"></i>
                </a>
                <a href="#" target="_blank">
                    <i className="fab fa-instagram fa-lg icon"></i>
                </a>
                

                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsibleNavbar">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="collapsibleNavbar">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                            <a className="nav-link" href="#landing">Home</a>
                        </li>
                    </ul>
                    <ul className="navbar-nav">
                        <li className="nav-item">
                            <a className="nav-link" href="#about">About</a>
                        </li>
                    </ul>
                    <ul className="navbar-nav">
                        <li className="nav-item">
                            <a className="nav-link" href="#podcasts">Podcasts</a>
                        </li>
                    </ul>
                    <ul className="navbar-nav">
                        <li className="nav-item">
                            <a className="nav-link" href="#contact">Contact</a>
                        </li>
                    </ul>
                </div>

            </nav>
        )
    }
}

export default Nav