import React from "react"
import PodcastsContent from "./PodcastsContent"
import podcastData from "./podcastData"

class Podcasts extends React.Component{

    render(){
        const podcastItem = podcastData.map(item => <PodcastsContent key={item.id} imgUrl={item.imgUrl} name={item.name} date={item.date} guestName={item.guestName}/>)

        return(
                <section id="podcasts">
                <div className="container-fluid">
                    <div className="row">
                        <h1 className="m-auto outbox">Bridcasts</h1>
                    </div>
                    {podcastItem}
                </div>
            </section>
        )
    }
}

export default Podcasts