import React from "react"
import AboutPerson from "./AboutPerson"
import aboutData from "./AboutData"
class About extends React.Component{
    render(){

        const aboutItem = aboutData.map(item => <AboutPerson key={item.id} imgUrl={item.imgUrl} name={item.name} twitter={item.twitter} instagram={item.instagram} bio={item.bio}  />)
        return(
            <section id="about" className="pb-2">
                <div className="container-fluid">
                    <h1 className="about-heading text-center outbox">Meet the guys</h1>
                    <hr className="about-hr" />
                    <div className="row">
                    <div className="card-deck col-12 d-flex justify-content-center">
                        {aboutItem} 
                    </div>
                    </div>
                </div>
            </section>
        )
    }
}

export default About