import React from "react"

function PodcastsContent(props) {

        return(
            <div className="row mb-2 col-12-wrap">
                <div className="col-12 col-lg-6 col-xl-4 ml-auto">
                    <img className="pb-3" src={props.imgUrl} width="100%"/>
                </div>
                <div className="col-12 col-lg-6 col-xl-4 mt-auto mb-auto mr-auto pb-5">
                <h2 className="outbox">{props.name}</h2>
                    <p>{props.date}</p> 
                    <p className="guest-name outbox">{props.guestName}</p>
                    <p>Guest Bio: Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
                    </p>
                    <a href="#" target="_blank">
                        <i className="fab fa-soundcloud fa-lg icon"></i>
                    </a>
                    <a href="#">
                        <i className="fab fa-youtube fa-lg icon"></i>
                    </a>
                    <a href="#" target="_blank">
                        <i className="fas fa-download fa-lg icon" data-toggle="tooltip" title="Coming soon"></i>
                    </a>                
                </div>
        </div>

        )
}

export default PodcastsContent