import React from "react"
import Footer from "./Footer"

class Contact extends React.Component {
    render(){
        return(
            <section id="contact" className="mt-5 pb-5 pt-5">
                <h1 className="text-center outbox pb-3">Contact</h1>
                <form className="m-auto col-12 col-md-8 col-lg-6 col-xl-4">
                    <div className="form-group">
                        <input type="email" className="form-control" placeholder="Enter email" id="email" />
                    </div>
                    <div className="form-group">
                        <input type="text" className="form-control" placeholder="Enter name" id="name" />
                    </div>
                    <div className="form-group">
                        <textarea type="text" rows="8" className="form-control" placeholder="Your message" id="msg" />
                    </div>
                    <button type="submit" className="col-12 btn btn-primary">Submit</button>
                </form>
                {/*Footer in bottom of contact to also use background from contact*/}
                <Footer />
            </section>
        )
    }
}

export default Contact