import React from "react"
import LoadingGif from "../imgs/loading.gif"

class Loading extends React.Component{
    render(){
        return(
            <div className="container-fluid">
                <div className="row">
                    <div className="m-auto loading-wrap">
                        <div className="text-center loading">
                            <img className="loading-gif animated rubberBand" src={LoadingGif} />
                            <h1 className="pt-5 animated rubberBand">Loading, won't be long!</h1>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Loading 