import React from "react"
import Nav from "./Nav"
import LandingContent from "./LandingContent"

class Landing extends React.Component{

    render(){
        return(
            <section id="landing">
                <div className="container-fluid">
                    <div className="row">
                        <Nav />
                        <LandingContent />
                    </div>
                </div>
            </section>
        )
    }
}

export default Landing